import React from 'react';
import Slider from '../../slider/Slider'
import earth from './home-img/heart.svg'
import plant from './home-img/plant.svg'
import bio from './home-img/bio.svg'
import '../../../App.css'
import './Home.css'
import './Home-cards.css'
import Navbar from '../../Navbar'
import ImgParallax from '../Homeparalax/parallax'
import Footer from '../../footer-import/containers/footer'
import CardItem from '../../product-card/CardItem'
import Pain from './home-img/IMG_9768.JPG'
import vins from './home-img/modified_IMG_9729.JPG'
import fruitlegumes from './home-img/IMG_9775.JPG'
import vrac from './home-img/IMG_9717.JPG'
import produitmoment from './home-img/promo.JPG'

export default function Home() {
  return (
    <>
    <Navbar />
    <Slider />

    <div className='labelContainer'>
      <div className='earthlogo'>
        <img src={earth} alt=""/>
        <h3>La bio respectueuse des hommes et de l'environement</h3>
      </div>
      <div  className='biologo'>
        <img src={bio} alt=""/>
        <h3>L'exigence qualité au dela des normes</h3>
      </div>
      <div className='plantlogo' >
        <img src={plant} alt=""/>
        <h3>Des actions pour developper les fillières bio</h3>
      </div>


    </div>
    <div className='cards'>
    <div className='cards'>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <CardItem
              src={Pain}
              text='Nos Pains'
              label=''
              path='/Pains'
            />
            <CardItem
              src={vins}
              text='Nos Vins Bio'
              label='Bientôt disponible'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src={produitmoment}
              text='Produits en promotion'
              label='Bientôt disponible'
            />
            <CardItem
              src={vrac}
              text='Le rayon Vrac'
              label=''
              path='/Vrac'
            />
            <CardItem
              src={fruitlegumes}
              text='Nos Fruits et légumes'
              label=''
              path='/FruitsLegumes'
            />
          </ul>
        </div>
      </div>
    </div>
  </div>  
    <ImgParallax />

    <Footer />

    </>
  );
  };
